.main-content {
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    margin-left: 10px; /* Disesuaikan untuk memberikan ruang pada sidebar */
  }
  /* Container utama untuk konten halaman */
  .data-obat-konten {
    padding: 20px;
    background-color: #f4f5f7;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  /* Header konten */
  .content-header {
    display: flex;
    align-items: center;
    gap: 10px; /* Jarak antara ikon dan teks */
    margin-bottom: 20px;
  }
  
  .content-header img {
    width: 24px; /* Ukuran ikon tas */
    height: 24px;
  }
  
  .content-header h1 {
    font-size: 1.5rem;
    font-weight: bold;
    color: #007bff; /* Warna teks sesuai gambar */
    margin: 0;
  }
  
  /* Kontainer detail obat */
  .detail-obat {
    background-color: white;
    border-radius: 12px;
    padding: 20px 40px;
    width: 100%;
    max-width: 600px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    align-items: center;
  }
  
  /* Kolom input */
  .kolom-obat {
    margin-bottom: 15px;
  }
  
  label {
    display: block;
    font-size: 14px;
    font-weight: 500;
    color: #6c757d;
    margin-bottom: 5px;
  }
  
  input {
    width: 100%;
    padding: 12px;
    border: 1px solid #e4e9ef;
    border-radius: 8px;
    background-color: #f4f5f7;
    color: #495057;
    font-size: 14px;
    box-sizing: border-box;
  }
  
  input[readonly] {
    cursor: not-allowed;
    background-color: #e9ecef;
  }
  
  .tombol-kembali {
    display: inline-block;
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 14px;
    text-align: center;
    text-decoration: none;
  }
  
  .tombol-kembali:hover {
    background-color: #0056b3;
  }
  
  /* Responsif untuk layar kecil */
  @media (max-width: 768px) {
    .detail-obat {
      padding: 20px;
      max-width: 90%;
    }
  
    .content-header h1 {
      font-size: 20px;
    }
  
    label,
    input,
    .tombol-kembali {
      font-size: 14px;
    }
  }