.dashboard-layout {
    display: flex;
  }
  
  .sidebar {
    width: 250px;
    background: #f8f9fa;
    padding: 20px;
    height: 100vh;
  }
  
  .sidebar-header .logo {
    width: 150px;
    margin-bottom: 20px;
  }
  
  .sidebar-menu li {
    list-style: none;
    margin: 15px 0;
  }
  
  .sidebar-menu li a {
    text-decoration: none;
    color: #333;
    font-weight: bold;
  }
  
  .content {
    flex: 1;
    padding: 20px;
  }
  
  header h1 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .entries select,
  .search input {
    padding: 5px;
    margin-right: 10px;
  }
  
  .add-button {
    padding: 5px 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .add-button:hover {
    background-color: #0056b3;
  }
  
  .data-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .data-table th,
  .data-table td {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  .data-table th {
    background-color: #f2f2f2;
  }
  
  .edit {
    background-color: #4867ef;
    border: none;
    padding: 5px 10px;
    color: #fff;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .delete {
    background-color: #dc3545;
    border: none;
    padding: 5px 10px;
    color: #fff;
    border-radius: 4px;
    cursor: pointer;
  }
  
  /* ViewJabatan.css */
  .popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Overlay gelap */
    display: flex;
    justify-content: center; /* Posisi horizontal tengah */
    align-items: center; /* Posisi vertikal tengah */
    z-index: 1000;
  }
  
  .popup {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    max-width: 400px; /* Batasi lebar */
    width: 100%;
    box-sizing: border-box; /* Pastikan padding tidak menambah lebar */
    text-align: center;
  }
  
  .popup h2 {
    margin-bottom: 20px;
    font-size: 1.5rem;
  }
  
  .popup form {
    margin-bottom: 20px;
  }
  
  .popup label {
    display: block;
    margin-bottom: 10px;
    text-align: left;
  }
  
  .popup input {
    width: 100%;
    padding: 8px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .popup-actions {
    display: flex;
    justify-content: space-between;
  }
  
  .cancel-button,
  .save-button {
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .cancel-button {
    background-color: #f44336;
    color: white;
  }
  
  .save-button {
    background-color: #4caf50;
    color: white;
  }
  
  /* Animasi */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: scale(0.9);
    }
    to {
      opacity: 1;
      transform: scale(1);
    }
  }