.kelola-stok-container {
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    max-width: 600px;
    margin: 0 auto;
  }
  
  .kelola-stok-title {
    font-size: 1.5rem;
    font-weight: bold;
    color: #333;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .kelola-stok-form {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .form-group {
    display: flex;
    flex-direction: column;
  }
  
  label {
    font-size: 1rem;
    font-weight: 600;
    margin-bottom: 5px;
    color: #555;
  }
  
  .required {
    color: red;
  }
  
  .form-control {
    padding: 10px;
    font-size: 1rem;
    border: 1px solid #ddd;
    border-radius: 5px;
    background-color: #f4f4f4;
    color: #555;
  }
  
  .form-control:read-only {
    background-color: #e9e9e9;
  }
  
  .kelola-stok-actions {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
  }
  
  .btn {
    padding: 10px 20px;
    font-size: 1rem;
    font-weight: bold;
    border-radius: 5px;
    cursor: pointer;
    border: none;
  }
  
  .cancel-btn {
    background-color: #ddd;
    color: #555;
    transition: background-color 0.3s;
  }
  
  .cancel-btn:hover {
    background-color: #bbb;
  }
  
  .save-btn {
    background-color: #007bff;
    color: #fff;
    transition: background-color 0.3s;
  }
  
  .save-btn:hover {
    background-color: #0056b3;
  }